@use "../../assets/sass/variables" as *;

.employeeCardsContainer {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 20px;
  margin: 0px 70px 0px 70px;
  justify-content: center;
}

.employeeCard {
  border-radius: 25px;
  height: 250px;
  width: 230px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.jsd {
  background-color: rgb(246, 224, 251);
}
.sd {
  background-color: rgb(205, 230, 244);
}
.ssd {
  background-color: rgb(217, 245, 213);
}
.pm {
  background-color: rgb(250, 255, 228);
}
.tst {
  background-color: rgb(246, 229, 205);
}
.hm {
  background-color: rgb(255, 228, 225);
}
